<template>
  <div v-loading="loading" element-loading-text="">
    <!--  -->
    <div class="btn">
      <el-button type="primary" @click="addDangerFace"></el-button>
    </div>
    <el-dialog title="" :visible.sync="dialogDangerFace" :close-on-click-modal="false">
      <div class="box">
        <el-form :model="form" label-width="100px">
          <el-form-item label="">
            <el-input v-model="form.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.number" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.notify_score" placeholder="(, 78.9)"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input
              type="textarea"
              v-model="notify_user"
              placeholder=", (: 13212312312,13212312312)"
            ></el-input>
          </el-form-item>

          <el-form-item label="">
            <div style="font-size: 20px; margin-bottom: 30px;">
              <el-select v-model="form.address" placeholder="" @change="addressChange">
                <el-option
                  v-for="item in addressList"
                  :key="item.id"
                  :label="item.address"
                  :value="item.id"
                ></el-option>
              </el-select>
            </div>

            <!-- <el-input v-model="form.address_id" style="width: 220px"></el-input> -->
          </el-form-item>
          <el-form-item label="">
            <el-upload
              action="https://api.fengniaotuangou.cn/api/upload"
              ref="upload"
              :limit="1"
              :before-upload="beforeAvatarUpload"
              :on-change="handleChange"
              :on-success="handleAvatarSuccess"
              :on-remove="handleRemove"
              :on-exceed="handleExceed"
              :auto-upload="false"
            >
              <el-button size="small" type="primary"></el-button>
            </el-upload>
            <div v-if="hasNewImage" style="color: red; font-size: 12px;">* </div>

            <div class="up-img">
              <img class="pic-box" :src="form.href" />
            </div>
          </el-form-item>
          <div class="submit">
            <el-form-item>
              <el-button type="primary" @click="upload"></el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </el-dialog>

    <el-table :data="tableData" empty-text="">
      <el-table-column prop="id" label="ID" align="center"></el-table-column>
      <el-table-column prop label="ID" align="center"></el-table-column>
      <el-table-column prop="name" label="" align="center"></el-table-column>
      <el-table-column prop="created_at" label="" align="center"></el-table-column>
      <el-table-column prop="number" label="" align="center"></el-table-column>
      <el-table-column prop="href" label="" align="center">
        <template slot-scope="scope">
          <div v-if="scope.row.href">
            <el-popover placement="top-start" title trigger="click">
              <img :src="scope.row.href" style="max-width:800px;max-height:800px;" />
              <img slot="reference" :src="scope.row.href" style="max-width:180px;max-height:80px;" />
            </el-popover>
          </div>
          <div v-else>
            <span>----</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" width="300px">
        <template slot-scope="scope">
          <el-button type="primary" size="mini" @click="handleLogs(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <!-- <el-dialog
      :visible.sync="dialogDel"
      title=""
      width="20%"
      align="center"
      :close-on-click-modal="false"
     >
      <div style="font-size: 20px; margin-bottom: 30px;"></div>
      <span>
        <el-button type="primary" @click="toDel"></el-button>
        <el-button type="danger" @click="dialogDel = false"></el-button>
      </span>
    </el-dialog>-->

    <!--  -->
    <el-dialog title="" :visible.sync="dialogLogs">
      <div class="box">
        <el-table :data="logsData">
          <el-table-column prop="danger.name" label="" align="center"></el-table-column>
          <el-table-column prop="address" label="" align="center"></el-table-column>
          <el-table-column prop="danger.number" label="" align="center"></el-table-column>
          <!-- <el-table-column prop="score" label="" align="center"></el-table-column> -->
          <el-table-column prop="log.timestamp" label="" align="center"></el-table-column>
          <el-table-column prop="danger.href" label="" align="center">
            <template slot-scope="scope">
              <div v-if="scope.row.danger.href">
                <el-popover placement="top-start" title trigger="click">
                  <img :src="scope.row.danger.href" style="max-width:800px;max-height:800px;" />
                  <img
                    slot="reference"
                    :src="scope.row.danger.href"
                    style="max-width:180px;max-height:80px;"
                  />
                </el-popover>
              </div>
              <div v-else>
                <span>----</span>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="block">
        <el-pagination
          @current-change="handleCurrentLogs"
          :current-page.sync="currentLogsPage"
          :page-sizes="[10, 20, 30, 40, 50]"
          :page-size="pageSizeLogs"
          layout="sizes, prev, pager, next, jumper"
          :total="totalLogsPage"
          @size-change="handleSizeLogs"
        ></el-pagination>
      </div>
    </el-dialog>
    <!--  -->
    <div class="block">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="pageSize"
        layout="sizes, prev, pager, next, jumper"
        :total="totalPage"
        @size-change="handleSizeChange"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import API from "@/api//index.js";
import { log } from "util";
import date from "../../utils/date";

export default {
  data() {
    return {
      loading: false,
      tableData: [], // 
      currentPage: 1, // 
      pageSize: 10,
      totalPage: 0,
      dialogDangerFace: false,
      // dialogPicture: false,
      // form: {
      //   href: "",
      // },
      qiniuaddr: "https://tu.fengniaotuangou.cn", // 

      hasNewImage: false,
      form: {
        name: "",
        number: "",
        href: "",
        notify_score: "", // 
        notify_user: "", // 
        address_id: "",
      },
      addressList: [],
      // dialogDel: false,
      dialogLogs: false,
      logsData: [],
      danger_id: "",
      currentLogsPage: 1,
      pageSizeLogs: 10,
      totalLogsPage: 0,
      notify_user: "",
    };
  },

  mounted() {
    this.getDangerFace();
  },
  methods: {
    // 
    getDangerFace() {
      var self = this;
      API.dangerFace(self.currentPage, self.pageSize).then((res) => {
        console.log("getDangerFace", res.data.data);
        self.tableData = res.data.data;
        self.totalPage = res.data.total;
      });
    },
    // 
    getAddress() {
      var self = this;
      API.address(self.currentPage, self.pageSize).then((res) => {
        console.log("getAddress", res);
        self.addressList = res.data;
      });
    },
    // 
    addressChange(val) {
      var self = this;
      self.form.address_id = val;
      console.log(self.form.address_id);
    },
    // 
    addDangerFace() {
      var self = this;
      self.dialogDangerFace = true;
      self.getAddress();
    },
    // 
    handleLogs(index, row) {
      var self = this;
      self.dialogLogs = true;
      self.danger_id = row.danger_id;
      console.log(row);
      self.getFaceLogs();
    },
    getFaceLogs() {
      var self = this;
      API.dangerLog(
        self.currentLogsPage,
        self.pageSizeLogs,
        self.danger_id
      ).then((res) => {
        console.log("getFaceLogs", res.data.data);
        self.logsData = res.data.data;
        self.totalLogsPage = res.data.total;
        self.logsData.forEach((item) => {
          item.log.timestamp = date.formatTime(
            item.log.timestamp,
            "Y-M-D h:m:s"
          );
        });
      });
    },

    handleRemove(file, fileList) {
      //
      var self = this;
      self.form.href = "";
      self.hasNewImage = false;
    },
    beforeAvatarUpload(file) {
      //
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isLt2M;
    },
    handleChange(file) {
      var self = this;
      self.form.href = URL.createObjectURL(file.raw);
      self.hasNewImage = true;
    },
    handleAvatarSuccess(res, file) {
      //
      var self = this;
      file.url = res.data;
      // self.href = file.url;
      self.form.href = file.url;
      //  '+86'
      var phones = self.notify_user.split(",");
      var arr = [];
      phones.forEach((item) => {
        item = "+86" + item;
        arr.push(item);
      });
      self.form.notify_user = arr.toString();
      API.addDangerFace(self.form).then((res) => {
        self.$message.success("");
        self.currentPage = 1;
        self.getDangerFace();
        self.$refs.upload.clearFiles();
        self.form = {
          name: "",
          number: "",
          href: "",
          notify_score: "",
          notify_user: "",
          address_id: "",
        };
        self.notify_user = ''
        // self.form.href = "";
        self.dialogDangerFace = false;
      });
    },
    handleExceed(files, fileList) {
      //
      var self = this;
      self.$message.error("1!");
      self.$refs.upload.clearFiles();
      self.form.href = "";
      self.form.id = "";
    },
    // 
    upload() {
      var self = this;
      self.$refs.upload.submit();
    },
    getQiniuToken() {
      var self = this;
      axios
        .get("https://api.fengniaotuangou.cn/api/upload/token")
        .then((res) => {
          self.imgData.token = res.data.uptoken;
        });
    },

    // 
    handleCurrentChange(val) {
      var self = this;
      console.log(val);
      self.currentPage = val;
      API.dangerFace(val, self.pageSize).then((res) => {
        self.tableData = res.data.data;
      });
    },
    // 
    handleSizeChange(val) {
      var self = this;
      console.log(val);
      self.pageSize = val;
      API.dangerFace(self.currentPage, val).then((res) => {
        self.tableData = res.data.data;
        self.totalPage = res.data.total;
        self.currentPage = 1;
      });
    },

    // 
    handleCurrentLogs(val) {
      var self = this;
      self.currentLogsPage = val;
      API.dangerLog(val, self.pageSizeLogs, self.danger_id).then((res) => {
        self.logsData = res.data.data;
        self.logsData.forEach((item) => {
          item.log.timestamp = date.formatTime(
            item.log.timestamp,
            "Y-M-D h:m:s"
          );
        });
      });
    },
    handleSizeLogs(val) {
      var self = this;
      self.pageSizeLogs = val;
      API.dangerLog(self.currentLogsPage, val, self.danger_id).then((res) => {
        self.logsData = res.data.data;
        self.logsData.forEach((item) => {
          item.log.timestamp = date.formatTime(
            item.log.timestamp,
            "Y-M-D h:m:s"
          );
        });
      });
    },
  },
};
</script>

<style scoped>
.upload-btn {
  margin-top: 10px;
}
.pic-box {
  max-width: 100%;
  height: 200px;
}
</style>